import React from "react";

function Calculator() {
  return (
    <div className="w-[50%] p-10">
      <div className="flex items-center justify-between">
        <p className="text-slate-900 font-bold text-[25px]">
          Mortgage Calculator
        </p>
        <button className="text-[18px] text-slate-700 underline underline-offset-2 cursor-pointer">
          Clear All
        </button>
      </div>
      <form className="mt-10">
        <label htmlFor="amount">Mortgage Amount</label>
        <div>
          <div>Â£</div>
          <input type="number" id="amount" value="" />
        </div>

        <div>
          <div>
            <label htmlFor="term">Mortgage Term</label>
            <div>
              <input type="number" id="term" value="" />
              <div>years</div>
            </div>
          </div>
          <div>
            <label htmlFor="rate">Interest Rate</label>
            <div>
              <input type="number" id="rate" value="" />
              <div>%</div>
            </div>
          </div>
        </div>

        <fieldset>
          <legend>Mortgage Type</legend>
          <label htmlFor="repayment">
            <input type="radio" id="repayment" /> Repayment
          </label>
          <label htmlFor="interest">
            <input type="radio" id="interest" /> Interests Only
          </label>
        </fieldset>

        <button type="submit">Calculate Repayments</button>
      </form>
    </div>
  );
}

export default Calculator;
